/* eslint-disable no-undef */
import jsPDF from "jspdf";
import "jspdf-autotable";
import { getToday, getUserdata, localStoragedecryp } from "../../../../../components/helpers/function";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const CetakLaporanTerimaBarang = (
  data = "",
  username = "",
  tanggal = "",
) => {
  // initialize jsPDF
  const doc = new jsPDF("l", "mm", [297, 210]);
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let footRows = [];
  let tableColumn = [];
  let finalY = 30;
  let total_masuk = 0;
  let total_keluar = 0;


  doc.setFontSize(15);
  doc.text("LAPORAN KEUANGAN DETAIL", 14, 15);
  doc.setFontSize(20);
  doc.text(getUserdata().nama_toko, 200, 15);
  doc.setFontSize(8);
  let jml_alamat = getUserdata().alamat_toko.length;
  if (jml_alamat > 20) {
    doc.text(getUserdata().alamat_toko.slice(0,48), 200, 20);
  }
  if (jml_alamat > 50) {
    doc.text(getUserdata().alamat_toko.slice(48,90), 200, 25);
  }
  doc.setFontSize(10);
  doc.setProperties({
    title: "LAPORAN KEUANGAN DETAIL",
  });
  doc.text(
    `Tanggal : ${
      localStoragedecryp("tgl_laporan").length === 0
        ? null
        : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal
    }`,
    14,
    25
  );
  data.forEach((item, index) => {
  tableColumn = [
    [
      {
        content: `NO`,
      },
      {
        content: `KATEGORI`,
      },
      {
        content: `DESKRIPSI`,
      },
      {
        content: `UANG MASUK`,
        styles: {
          halign: 'right'
        },
      },
      {
        content: `UANG KELUAR`,
        styles: {
          halign: 'right'
        },
      },
    ],
  ];

    // item.list_barang.forEach((barang, index) => {
    let dataku = item.deskripsi;
    let hasilku = dataku.split('<br/>');
    let hasilakhir = "";
    if (hasilku.length > 2) {
      for (let ia = 1; ia < hasilku.length; ia++) {
        if (hasilakhir === "") {
          hasilakhir += hasilku[ia];
        } else {
        hasilakhir += `\n` + hasilku[ia];
        }
      }
    } else {
      hasilakhir = hasilku[1]  
    }
    let rows = [
      ++index,
      item.kategori,
      hasilakhir,
      {
        content: parseInt(item.jumlah_in).toLocaleString("kr-KO"),
        styles: {
          halign: 'right'
        },
      },
      {
        content: parseInt(item.jumlah_out).toLocaleString("kr-KO"),
        styles: {
          halign: 'right'
        },
      }
    ];
    total_masuk = total_masuk + item.jumlah_in;
    total_keluar = total_keluar + item.jumlah_out;
    tableRows.push(rows);

  });
  let footer = [
    {
      content: ``,
      colSpan: 3,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content:  `${total_masuk.toLocaleString("kr-KO")}`,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
        halign: 'right'
      },
    },
    {
      content:  `${total_keluar.toLocaleString("kr-KO")}`,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
        halign: 'right'
      },
    },
  ];
  footRows.push(footer);
  let footer2 = [
    {
      content: `Saldo Akhir`,
      colSpan: 4,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
        halign: 'right'
      },
    },
    {
      content:  `${(total_masuk - total_keluar).toLocaleString("kr-KO")}`,
      styles: {
        halign: 'right',
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
  ];
  footRows.push(footer2);
  let printed = [
    {
      content: `Printed By ${JSON.parse(localStoragedecryp("userdata")).user_id} / ${getToday()}`,
      colSpan: 5,
      styles: {
        // lineWidth: 0.1,
        fontStyle: "italic",
        textColor: "#000",
        // fillColor: "#E8E5E5"
      },
    },
  ]
  footRows.push(printed);
  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    foot: footRows,
    startY: finalY,
    theme: "plain",
    rowPageBreak: "avoid",
    // pageBreak: "avoid",
    margin: { top: 10 },
    bodyStyles: {
      // lineWidth: 0.02,
      // lineColor: "#000",
      fontSize: 8,
    },
    headStyles: {
      fontSize: 8,
      // lineWidth: 0.02,
      // lineColor: "#000",
      fillColor: "#E8E5E5",
      textColor: "#000",
    },
    tableLineColor: [255, 255, 255],
    tableLineWidth: 1,
  });
  finalY = doc.autoTableEndPosY() + 3;
  tableRows = [];
  footRows = [];
  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left
  // doc.text(`User                           : ${username}`, 14, finalY + 10);
  // doc.text(`Cetak                        : ${getToday()}`, 14, finalY + 16);
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  // doc.autoPrint();
  // doc.save(`LAPORAN KEUANGAN DETAIL.pdf`);
  var string = doc.output("datauristring");
  PrintPenjualan.postMessage(string);
  // var embed = "<embed width='100%' height='100%' src='" + string + "'/>";
  // var x = window.open();
  // x.document.open();
  // x.document.write(
  //   "<html><head><title>LAPORAN KEUANGAN DETAIL</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
  //     string +
  //     "></embed></body></html>"
  // );
  // x.document.write(embed);
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default CetakLaporanTerimaBarang;

