import React, { Component, lazy } from "react";
import Skeleton from "react-loading-skeleton";
import { Link } from "react-router-dom";
import { NotifInfo, ToastNotification } from "../../../../components/helpers/notification";
import {
  Panel,
  PanelBody,
  PanelHeader,
} from "../../../../components/panel/panel";
import ToolkitProvider, {
  Search,
  // CSVExport,
} from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";

import { postDataNoTImeOut } from "../../../../config/axios";
import BootstrapTable from "react-bootstrap-table-next";
import { formatDate, localStoragedecryp, localStorageencryp } from "../../../../components/helpers/function.jsx";
import CetakLaporanRekap from "./pdf/CetakLaporanRekap";
import CetakLaporanKeuanganDetail from "./pdf/CetakLaporanKeuanganDetail";
// import { disableButtonValidasi } from "../../../../actions/hutang_action";
import * as moment from "moment-timezone";
import LaporanKeaunagnRekap from "./exel/LaporanKeaunagnRekap";
// import LaporanKeuanganDetail from "./exel/LaporanKeuanganDetail";
import { PageSettings } from "../../../../config/page-settings";
const HeadLaporanKeuanganKas = lazy(() => import("./HeadLaporanKeuanganKas"));


const { SearchBar } = Search;
// const { ExportCSVButton } = CSVExport;

class LaporanKeuanganKas extends Component {
  static contextType = PageSettings;
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      hidden: false,
      istypeRekap: false,
      tanggal: false,
      LaporanKeuanganDetail: [],
      LaporanKeuanganReakap: [],
      columnsdetail: [
        {
          dataField: "kategori",
          text: "Kategori",
          formatter: (data) => {
            return data === undefined ? "-" : data;
          },
          csvFormatter: (data) => {
            return data === undefined ? '-' : data;
          },
          footerAttrs: {
            'colSpan': '3'
          },
          // footer: "Total"
        },
        {
          dataField: "deskripsi",
          text: "Deskripsi",
          formatter: (data) => {
            console.log(data);
               // console.log(data);
            let row = data || "-";
            let hasil = row.split('<br/>');
            return data === undefined ? "-" : <> {hasil[1]} <br/> {hasil[2]} </>;
          },
          csvFormatter: (data) => {
            return data === undefined ? '-' : data;
          },
        },
        {
          dataField: "jumlah_in",
          text: "Uang Masuk",
          headerClasses: 'text-right',
          formatter: (data) => {
            return <div className="text-right"> {data === undefined || null ? 0 : parseFloat(data).toLocaleString("kr-KO")}</div>;
          },
          csvFormatter: (data) => {
            return <div className="text-right"> {data === undefined || null ? '0' : data} </div>
          },
          // footer: (columnData) =>
          // <div className="text-right"> {parseInt(
          //   columnData.reduce((acc, item) => acc + item, 0)
          // ).toLocaleString("kr-KO")} </div>
        },
        {
          dataField: "jumlah_out",
          text: "Uang Keluar",
          headerClasses: 'text-right',
          formatter: (data) => {
            return <div className="text-right"> {data === undefined || null ? 0 : parseFloat(data).toLocaleString("kr-KO")}</div>;
          },
          csvFormatter: (data) => {
            return <div className="text-right"> {data === undefined || null ? '0' : data} </div>
          },
          // footer: (columnData) =>
          // <div className="text-right"> {parseInt(
          //   columnData.reduce((acc, item) => acc + item, 0)
          // ).toLocaleString("kr-KO")} </div>
        },
      ],
      columnsrekap: [
        {
          dataField: "_id",
          text: "Kategori",
          footer: 'Total',
        },
        {
          dataField: "jumlah_in",
          text: "Uang Masuk",
          headerClasses: 'text-right',
          formatter: (data) => {
            return <div className="text-right"> {data === undefined || null ? 0 : parseFloat(data).toLocaleString("kr-KO")}</div>;
          },
          csvFormatter: (data) => {
            return <div className="text-right"> {data === undefined || null ? '0' : data} </div>
          },
          footer: (columnData) =>
          <div className="text-right"> {parseInt(
            columnData.reduce((acc, item) => acc + item, 0)
          ).toLocaleString("kr-KO")} </div>
        },
        {
          dataField: "jumlah_out",
          text: "Uang Keluar",
          headerClasses: 'text-right',
          formatter: (data) => {
            return <div className="text-right"> {data === undefined || null ? 0 : parseFloat(data).toLocaleString("kr-KO")}</div>;
          },
          csvFormatter: (data) => {
            return <div className="text-right"> {data === undefined || null ? '0' : data} </div>
          },
          footer: (columnData) =>
          <div className="text-right"> {parseInt(
            columnData.reduce((acc, item) => acc + item, 0)
          ).toLocaleString("kr-KO")} </div>
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  handleSubmit(data) {
    let data_post = {
      tanggal : formatDate(moment(new Date(data.tgl_awal)).format('YYYY-MM-DD')),
    };
    let data_post1 = {
      tgl_awal : formatDate(moment(new Date(data.tgl_awal)).format('YYYY-MM-DD')),
    };
    this.setState({ tanggal: data.tanggal_awal });
    this.setState({ isLoading: true });
    if (data.type === "detail") {
      postDataNoTImeOut("reportcash/detail", data_post)
        .then((res) => {
          // console.log(res.data);
          // return false
          if (res.data.length === 0) {
            ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
            this.setState({
              LaporanKeuanganDetail: [],
            });
          } else {
            localStorageencryp('tgl_laporan', JSON.stringify(data_post1))
            let no = 1;
            let databarang = [];
            res.data.forEach(element => {
              let row = {
                no: no,
                kategori : element.kategori,
                deskripsi : element.deskripsi,
                jumlah_in : element.jumlah_in,
                jumlah_out : element.jumlah_out,
              }
              databarang.push(row)
              no++;
            });
            this.setState({ LaporanKeuanganDetail: databarang });
          }

        })
        .then(() => {
          this.setState({ isLoading: false });
        })
        .catch((error) => {
          this.setState({ isLoading: false });
          this.setState({
            LaporanKeuanganDetail: [],
          });
          NotifInfo(error)});
    }

    if (data.type === "rekap") {
      postDataNoTImeOut("reportcash/rekap", data_post)
        .then((res) => {
          if (res.data.length === 0) {
            ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
            this.setState({
              LaporanKeuanganReakap: [],
            });
          } else {
            ToastNotification("success", "Laporan Keuangan Tersedia");
            localStorageencryp('tgl_laporan', JSON.stringify(data_post1))
            let no = 1;
            let databarang = [];
            res.data.forEach(element => {
              let row = {
                no: no,
                _id: element._id,
                deskripsi: element.deskripsi,
                jumlah_in: element.jumlah_in,
                jumlah_out: element.jumlah_out,
              }
              databarang.push(row)
              no++;
            })
            this.setState({
              LaporanKeuanganReakap: databarang,
            });
          }
        })
        .then(() => {
          this.setState({ isLoading: false });
        })
        .catch((error) => {
          this.setState({ isLoading: false });
          ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
        });
    }
  }
  componentDidMount() {
    let data = JSON.parse(localStoragedecryp("userdata"));
    this.setState({ username: data.user_id });
    this.context.handleSetPageSidebar(false);
    this.context.handleSetPageHeader(false);
  }
  onChange(e) {
    if (e.type === "rekap") {
      this.setState({ istypeRekap: true });
      this.setState({ LaporanKeuanganDetail: [] });
    } else {
      this.setState({ LaporanKeuanganReakap: [] });
      this.setState({ istypeRekap: false });
    }
  }
  render() {
    let total_uang_keluar_detail = 0;
    let total_uang_masuk_detail = 0;
    let hasil_laporan_keuangan_detail = 0;
    if (this.state.LaporanKeuanganDetail.length === 0) {
      // null
    }else{
      this.state.LaporanKeuanganDetail.forEach(element => {
        total_uang_keluar_detail = total_uang_keluar_detail + element.jumlah_out;
        total_uang_masuk_detail = total_uang_masuk_detail + element.jumlah_in;
      });
      hasil_laporan_keuangan_detail = parseInt(total_uang_masuk_detail) - parseInt(total_uang_keluar_detail);
    }

    let total_uang_keluar_rekap = 0;
    let total_uang_masuk_rekap = 0;
    let hasil_laporan_keuangan_rekap = 0;
    if (this.state.LaporanKeuanganReakap.length === 0) {
      // null
    }else{
      this.state.LaporanKeuanganReakap.forEach(element => {
        total_uang_keluar_rekap = total_uang_keluar_rekap + element.jumlah_out;
        total_uang_masuk_rekap = total_uang_masuk_rekap + element.jumlah_in;
      });
      hasil_laporan_keuangan_rekap = parseInt(total_uang_masuk_rekap) - parseInt(total_uang_keluar_rekap);
    }
    return (
      <div>
        <Link to="/SubMenuLaporan">
          <button
            type="button"
            className="btn btn-secondary"
            data-tut="reactour_4"
          >
            <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Home
          </button>
        </Link>
        <br />
        <br />
        <Panel>
          <PanelHeader>Laporan Keuangan Cash</PanelHeader>
          <PanelBody>
            {/* {console.log(this.state.hidden)} */}
            <HeadLaporanKeuanganKas
              onChange={(data) => this.onChange(data)}
              isLoading={this.state.isLoading}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </PanelBody>
        </Panel>
        <Panel>
          <PanelHeader>Laporan Keuangan Cash</PanelHeader>
          <br />
          {/* Laporan Keuangan */}
          <div className="container">
            {this.state.isLoading === false ? (
              <ToolkitProvider
                keyField="no"
                data={
                  this.state.istypeRekap === true
                    ? this.state.LaporanKeuanganReakap || []
                    : this.state.LaporanKeuanganDetail || []
                }
                columns={
                  this.state.istypeRekap === true
                    ? this.state.columnsrekap
                    : this.state.columnsdetail
                }
                search
                exportCSV={{
                  fileName: this.state.istypeRekap
                    ? "Laporan Rekap Keuangan.csv"
                    : "Laporan Detail Keuangan.csv",
                }}
              >
                {(props) => (
                  <div className="row">
                    {this.state.LaporanKeuanganReakap.length === 0 ? null : (
                      <>
                        {this.state.LaporanKeuanganReakap ? (
                          <>
                          <div className="col-lg-6"></div>
                            <div className="col-6">
                              <div className="text-right">
                                <SearchBar {...props.searchProps} />
                              </div>
                            </div>
                          </>
                        ) : null}
                      </>
                    )}

                    {this.state.LaporanKeuanganDetail.length === 0 ? null : (
                      <>
                        {this.state.LaporanKeuanganDetail ? (
                          <>
                            <div className="col-6">
                            {/* <h3> Saldo Akhir : {hasil_laporan_keuangan_detail.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")} </h3> */}
                            </div>
                            <div className="col-6">
                              <div className="text-right">
                                <SearchBar {...props.searchProps} />
                              </div>
                            </div>
                          </>
                        ) : null}
                      </>
                    )}
                    <hr />
                    <div className="col-12">
                      <BootstrapTable
                        pagination={paginationFactory()}
                        {...props.baseProps}
                      />
                      <br />
                    </div>
                    {this.state.LaporanKeuanganDetail.length === 0 ? null : (
                      <>
                        {this.state.LaporanKeuanganDetail ? (
                          <>
                          <div className="col-lg-6"></div>
                            <div className="col-6 text-right">
                            <h3> Uang Masuk : {total_uang_masuk_detail.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")} </h3>
                            </div>
                            <div className="col-lg-6"></div>
                            <div className="col-6 text-right">
                            <h3> Uang Keluar : {total_uang_keluar_detail.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")} </h3>
                            </div>
                            <div className="col-lg-6"></div>
                            <div className="col-6 text-right">
                            <h3>________________________________  - </h3>
                            </div>
                          <div className="col-lg-6"></div>
                            <div className="col-6 text-right">
                            <h3> Saldo Akhir : {hasil_laporan_keuangan_detail.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")} </h3>
                            </div>
                            {/* <div className="col-6">
                              <div className="text-right">
                                <SearchBar {...props.searchProps} />
                              </div>
                            </div> */}
                          </>
                        ) : null}
                      </>
                    )}
                    {this.state.LaporanKeuanganReakap.length === 0 ? null : (
                      <>
                        {this.state.LaporanKeuanganReakap ? (
                          <>
                          <div className="col-lg-6"></div>
                            <div className="col-6 text-right">
                            <h3> Uang Masuk : {total_uang_masuk_rekap.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")} </h3>
                            </div>
                            <div className="col-lg-6"></div>
                            <div className="col-6 text-right">
                            <h3> Uang Keluar : {total_uang_keluar_rekap.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")} </h3>
                            </div>
                            <div className="col-lg-6"></div>
                            <div className="col-6 text-right">
                            <h3>________________________________  - </h3>
                            </div>
                          <div className="col-lg-6"></div>
                          <div className="col-6 text-right">
                            <h3>Saldo Akhir : {hasil_laporan_keuangan_rekap.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</h3>
                            </div>
                            <div className="col-6">
                            <button
                                className="btn btn-warning btn-block"
                                onClick={() =>
                                  // {console.log( this.state.LaporanKeuanganReakap);}
                                  CetakLaporanRekap(
                                    this.state.LaporanKeuanganReakap,
                                    this.state.username,
                                    this.state.tanggal
                                  )
                                }
                              >
                               Export PDF
                              </button>
                            </div>
                            <div className="col-6">
                            <LaporanKeaunagnRekap data={ this.state.LaporanKeuanganReakap} />
                                
                            </div>
                          </>
                        ) : null}
                      </>
                    )}
                    {this.state.LaporanKeuanganDetail.length === 0 ? null : (
                      <>
                        {this.state.LaporanKeuanganDetail ? (
                          <>
                            <div className="col-12">
                            <button
                                className="btn btn-warning btn-block"
                                onClick={() =>
                                  // {console.log(this.state.LaporanKeuanganDetail);}
                                  CetakLaporanKeuanganDetail(
                                    this.state.LaporanKeuanganDetail,
                                    this.state.username,
                                    this.state.tanggal
                                  )
                                }
                              >
                               Export PDF
                              </button>
                             
                              
                            </div>
                            {/* <div className="col-6">
                             <LaporanKeuanganDetail data={ this.state.LaporanKeuanganDetail} />
                              &nbsp;
                            </div> */}
                          </>
                        ) : null}
                      </>
                    )}
                  </div>
                )}
              </ToolkitProvider>
            ) : (
              <Skeleton width={"100%"} height={200} />
            )}
          </div>
          <br />

          {/* End Tambah Master Baki  */}
        </Panel>
      </div>
    );
  }
}

export default LaporanKeuanganKas;
