import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { getUserdata, localStoragedecryp } from "../../../../../components/helpers/function";
class LaporanKeaunagnRekap extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {

    let total_masuk = 0;
    let total_keluar = 0;
      let no = 1;
    this.props.data.forEach((item, index) => {
        total_masuk = total_masuk + item.jumlah_in;
        total_keluar = total_keluar + item.jumlah_out;
    });
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Keuangan Rekap"
          sheet="Laporan Keuangan Rekap"
          buttonText="Export Exel"
        />
        <table rowkey="kode_member" id="table-to-xls" style={{ display: "none" }}>
        <thead>
          <tr>
            <th colSpan="3" style={{ textAlign: "center" }}>
              {" "}
              LAPORAN KEUANGAN REKAP{" "}
            </th>
          </tr>
          <tr>
                <th colSpan="3"> Tanggal  </th>
          </tr>
          <tr>
                <th colSpan="3"> {localStoragedecryp('tgl_laporan').length === 0 ? null : JSON.parse(localStoragedecryp('tgl_laporan')).tgl_awal } </th>
          </tr>
          <tr>
              <th colSpan="3"> TOKO :  {getUserdata().nama_toko} </th>
            </tr>
           
            <tr>
              <th colSpan="3"> ALAMAT :  {getUserdata().alamat_toko} </th>
            </tr>
          <tr>
              <th colSpan="3"></th>
            </tr>
            <tr >
                <th  style={{ backgroundColor: "#E8E5E5", color: "#000" }}>No</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>Uang Masuk</th>
                <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>Uang Keluar</th>
            </tr>
        </thead>
        <tbody>
          {this.props.data.map((item, index) => (
            <tr key={index}>
                <td> {no++}</td>
                <td style={{ textAlign: "right" }}>&nbsp; {item.jumlah_in.toLocaleString("kr-KO")}</td>
                <td style={{ textAlign: "right" }}>&nbsp; {item.jumlah_out.toLocaleString("kr-KO")}</td>
            </tr>
        ))}
        </tbody>
          <tfoot>
          <tr>
          <th style={{ backgroundColor: "#E8E5E5", color: "#000" }} > </th>
            <th style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right" }}>&nbsp;{total_masuk.toLocaleString("kr-KO")} </th>
            <th style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign : "right" }}>&nbsp;{total_keluar.toLocaleString("kr-KO")}</th>
          </tr>
          </tfoot>

        </table>
      </>
    );
  }
}
export default LaporanKeaunagnRekap;
